<template>
    <aside class="aside">
        <div class="info-card" v-if="infoCard">
            <div class="info-card-header" >
                <div class="card-header-img">
                    <img :src="infoCard.info_image" style="width: 100%;height: 100%">
                </div>
                <h3 class="card-header-title">{{ infoCard.info_name }}</h3>
                <p class="card-header-desc">{{ infoCard.info_desc }}</p>
            </div>
            <el-divider></el-divider>
            <div class="info-card-github">
                <a :href="infoCard.info_git"  target="_blank" v-if="infoCard.info_git">
                    <el-tooltip class="item" effect="dark" content="GitHub" placement="top">
                        <i class="iconfont icon-github" style="font-size: 25px"></i>
                    </el-tooltip>
                </a>
                <a :href="infoCard.info_twitter"  target="_blank" v-if="infoCard.info_twitter">
                    <el-tooltip class="item" effect="dark" content="Twitter" placement="top">
                        <i class="iconfont icon-twitter_F" style="font-size: 25px"></i>
                    </el-tooltip>
                </a>
                <a :href="infoCard.info_telegram"  target="_blank" v-if="infoCard.info_telegram">
                    <el-tooltip class="item" effect="dark" content="Telegram" placement="top">
                        <i class="iconfont icon-telegram" style="font-size: 25px"></i>
                    </el-tooltip>
                </a>
                <a :href="infoCard.info_weibo"  target="_blank" v-if="infoCard.info_weibo">
                    <el-tooltip class="item" effect="dark" content="微博" placement="top">
                        <i class="iconfont icon-weibo-copy" style="font-size: 25px"></i>
                    </el-tooltip>
                </a>
                <a :href="infoCard.info_wechat"  target="_blank" v-if="infoCard.info_wechat">
                    <el-tooltip class="item" effect="dark" content="微信" placement="top">
                        <i class="iconfont icon-weixin" style="font-size: 25px"></i>
                    </el-tooltip>
                </a>
            </div>
            <el-divider></el-divider>
        </div>
    </aside>
</template>

<script>
    export default {
        name: "CardAside",
        data(){
          return {
              infoCard: null,
          }
        },
        methods:{
            getCard:function () {
                var that = this;
                that.$getHttp.getAxios.bind(that)('get','/v1/InfoCard',).then(value => {
                    if (value.code === 200){
                        that.infoCard = value.data
                    }
                });
            }
        },
        mounted(){
            this.getCard();
        },
    }
</script>

<style scoped>
    .aside{
        float: right;
        width: 285px;
        background: #fff;
        border-radius: 4px;
    }
    .info-card{
        min-height: 150px;
    }
    .info-card-header{
        height: 230px;
        overflow:hidden;
        background: -webkit-linear-gradient(to bottom,#e6ebe5 25%, #fff 0); /* Safari 5.1 - 6.0 */
        background: -o-linear-gradient(to bottom,#e6ebe5 25%, #fff 0); /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(to bottom,#e6ebe5 25%, #fff 0); /* Firefox 3.6 - 15 */
        background: linear-gradient(to bottom,#e6ebe5 25%, #fff 0 );
    }
    .card-header-img{
        margin: 10px auto 10px;
        width: 120px;
        height: 120px;
        border-radius: 50%;
        border: solid 3px #fff;
        overflow: hidden;
    }
    .card-header-title{
        font-size: 24px;
        font-weight: 700;
        text-align: center;
    }
    .card-header-desc{
        font-size: 12px;
        padding: 16px;
        text-align: center;
    }
    .info-card-github{
        text-align: center;
        font-size: 22px;
    }
    .info-card-github i{
        margin: 0 10px;
    }
</style>