import store from '@/vuex/store'
import axios from 'axios'
import md5 from 'js-md5';

const app_id = 2026668444;
const grant_type = 'access_token';
const appSecret = 'i77E7x3zQX8PH6nQP6G6p7JmWXmmZ407';


function isGetAccessToken() {
    var that = this;
    if (store.state.access_token === ''){
        build_signature.bind(that)();
    }
}
//建立signature
function build_signature() {
    var getTimestamp = this.$public.getTimestamp();
    var str = 'app_id'+app_id+'grant_type'+grant_type+'timestamp'+getTimestamp;
    var signature = md5(appSecret+str+appSecret).toUpperCase();
    getAccessToken.bind(this)(signature,getTimestamp);
}


function getAccessToken(signature,getTimestamp) {
    var that = this;
    var url = 'http://api.blog.com/oauth/token?'+'app_id='+app_id+'&grant_type='+grant_type+'&timestamp='+getTimestamp+'&signature='+signature;
    axios({
        method: 'GET',
        url: url,
        data:{
            'app_id' : app_id,
            'grant_type' : grant_type,
            'timestamp' : getTimestamp,
            'signature' : signature
        },
        headers:{
            'Timestamp' : getTimestamp
        },
    })
        .then(function (res) {
            var resData = res.data;
            if (resData.code === 200){
                store.commit('editAccessToken',resData.data.access_token);
            }
            if (that.doCallback){
                that.doCallback();
                that.doCallback = null;
            }
        })
}

export default {
    isGetAccessToken
}