<template>
    <div class="posts">
        <base-header></base-header>
        <base-banner banner="文章"></base-banner>
        <div class="main">
            <posts-main :article="article"></posts-main>
            <card-aside></card-aside>
            <div style="clear: none"></div>
        </div>
        <base-footer></base-footer>
    </div>
</template>

<script>
    import BaseHeader from '@/pages/base/BaseHeader.vue'
    import BaseFooter from '@/pages/base/BaseFooter.vue'
    import BaseBanner from '@/pages/base/BaseBanner.vue'
    import PostsMain from '@/pages/posts/PostsMain.vue'
    import CardAside from '@/pages/aside/CardAside.vue'
    import store from '@/vuex/store'

    export default {
        name: "posts",
        data () {
            return {
                article:[]
            }
        },

        components: {
            BaseHeader,
            BaseFooter,
            BaseBanner,
            PostsMain,
            CardAside
        },
        methods:{
            doCallback:function () {
                var that = this;
                if (store.state.access_token !== ''){
                    that.$getHttp.getAxios.bind(that)('get','/v1/Articles',).then(value => {
                        if (value.code === 200){
                            that.article = value.data;
                        }
                    });
                }
            }
        },
        mounted(){
            this.$config.isGetAccessToken.bind(this)();
            this.doCallback();
        },
    }
</script>

<style scoped>
    .posts{
        margin: 0 auto;
    }
    .main {
        width: 1200px;
        margin: 15px auto;
        overflow: hidden;
    }

</style>