<template>
    <div class="header">
        <nav class="container">
            <div class="navbar" v-if="header">
                <router-link to="/home">
                    <span class="navbar-name">{{ header.title }}</span>&nbsp;
                    <span class="navbar-subheading">{{ header.subhead }}</span>
                </router-link>
            </div>
            <div class="navbar-links">
                <router-link to="/home" class="navbar-link">首页</router-link>
                <router-link to="/posts" class="navbar-link">文章</router-link>
                <router-link to="/about" class="navbar-link">关于我</router-link>
            </div>
        </nav>
    </div>
</template>

<script>
    export default {
        name: 'BaseHeader',
        data(){
          return{
              header:null
          }
        },
        methods:{
            getHeader:function () {
                var that = this;
                that.$getHttp.getAxios.bind(that)('get','/v1/header',).then(value => {
                    if (value.code === 200){
                        that.header = value.data
                    }
                });
            }
        },
        mounted(){
            this.getHeader();
        },
    }
</script>

