import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import posts from '@/components/posts'
import about from '@/components/about'
import post from '@/components/post'
import Error from '@/components/Error'

Vue.use(Router);

export default new Router({
    mode: 'history',
    //base: '/dist',
    routes: [
        {
            path: '/',
            redirect: '/home'
        },
        {
            path: '/home',
            name: 'home',
            component: home
        },
        {
            path: '/posts',
            name: 'posts',
            component: posts,
        },
        {
            path: '/about',
            name: 'about',
            component: about
        },
        {
            path: '/post',
            name: 'post',
            component: post
        },
        {
            path:'*',
            component: Error
        }
    ]
})