<template>
    <div class="post">
        <base-header></base-header>
        <base-banner banner="内容"></base-banner>
        <div class="main">
            <post-main :details="details"></post-main>
            <card-aside></card-aside>
            <div style="clear: none"></div>
        </div>
        <base-footer></base-footer>
    </div>
</template>

<script>
    import BaseHeader from '@/pages/base/BaseHeader.vue'
    import BaseFooter from '@/pages/base/BaseFooter.vue'
    import BaseBanner from '@/pages/base/BaseBanner.vue'
    import PostMain from '@/pages/posts/PostMain.vue'
    import CardAside from '@/pages/aside/CardAside.vue'
    import store from '@/vuex/store'

    export default {
        name: "post",
        components: {
            BaseHeader,
            BaseFooter,
            BaseBanner,
            PostMain,
            CardAside
        },
        data(){
            return {
                details:null,
            }
        },
        methods:{
            doCallback:function () {
                var that = this;
                if (store.state.access_token !== ''){
                    if (this.$route.query.id){
                        that.$getHttp.getAxios.bind(that)('get','/v1/Articles/'+this.$route.query.id,).then(value => {
                            if (value.code === 200){
                                that.details = value.data
                            }
                        });
                    }
                }
            }
        },
        mounted(){
            this.$config.isGetAccessToken.bind(this)();
            this.doCallback();
        },
    }
</script>

<style scoped>
    .post{
        margin: 0 auto;
    }
    .main{
        width: 1200px;
        margin: 15px auto;
        overflow: hidden;
    }
</style>