<template>
    <div class="about">
        <article class="article" v-if="about">
            <div class="article-content">
                <h3 class="article-title">
                    {{ about.title }}
                </h3>
                <p v-html="about.content">
                    {{ about.content }}
                </p>
            </div>
        </article>
    </div>
</template>

<script>
    export default {
        name: "AboutMain",
        data(){
            return {
                about: null,
            }
        },
        methods:{
            getAbout:function () {
                var that = this;
                that.$getHttp.getAxios.bind(that)('get','/v1/About',).then(value => {
                    if (value.code === 200){
                        that.about = value.data
                    }
                });
            }
        },
        mounted(){
            this.getAbout();
        },
    }
</script>

<style scoped>
    .about{
        float: left;
        width: 900px;
        border-radius: 2px;
        background: #fff;
    }
    .article{
        overflow: hidden;
    }
    .article-content{
        padding: 24px;
    }
    .article-title{
        padding: 24px 0;
        font-size: 20px;
        font-weight: 600;
    }
    .article-content>>>p{
        color: #666;
        line-height: 1.65;
        text-indent: 2em;
        padding-top: 24px;
        font-size: 16px;
    }
    .article-content>>>img{
        width: 100%;
        border-radius: 4px;
        border: 1px solid #ccc;
    }
</style>