<template>
    <div class="error" :style="{ height: screenHeight + 'px' }">
        <div class="circle">
            <p class="circle_404">404</p>
            <p class="circle_text">您要找的页面未找到。</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Error",
        data(){
            return {
                screenHeight: window.innerHeight,
            }
        },
        mounted () {
            const that = this;
            window.onresize = () => {
                return (() => {
                    that.screenHeight = window.innerHeight;
                })()
            }
        },
    }

</script>

<style scoped>
    .error{
        width: 100%;
        display: flex;
        align-items: center;
    }
    .circle{
        width: 260px;
        height: 260px;
        border-radius : 50%;
        background: #c0c6cc;
        margin: 0 auto;
    }
    .circle_404{
        text-align: center;
        line-height: 260px;
        font-size: 70px;
        color: #ffffff;
        font-weight: 900;
    }
    .circle_text{
        font-size: 20px;
        text-align: center;
        line-height: 50px;
    }
</style>