<template>
    <footer class="footer" v-if="footer">
        <p class="footer-link">
            <a :href="footer.icon_link" target="_blank">
                <i :class="footer.icon" style="font-size:20px"></i>
            </a>
        </p>
        <p class="git-links">{{ footer.footer_text }}</p>
    </footer>
</template>

<script>
    export default {
        name: "BaseFooter",
        data(){
            return{
                footer:null
            }
        },
        methods:{
            getFooter:function () {
                var that = this;
                that.$getHttp.getAxios.bind(that)('get','/v1/Footer',).then(value => {
                    if (value.code === 200){
                        that.footer = value.data
                    }
                });
            }
        },
        mounted(){
            this.getFooter();
        },
    }
</script>

<style scoped>
    .footer {
        width: 100%;
        height: 100px;
        text-align: center;
    }
    .footer-link{
        font-size: 24px;
        padding: 12px 0;
    }
    .git-links {
        display: inline-block;
        font-size: 16px;
        background: linear-gradient(left, #f71605, #e0f513);
        background: -webkit-linear-gradient(left, #f71605, #e0f513);
        background: -o-linear-gradient(right, #f71605, #e0f513);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        animation: scratchy 1s linear forwards infinite;
        /* 其它浏览器兼容性前缀 */
        -webkit-animation: scratchy 0.5s linear forwards infinite;
        -moz-animation: scratchy 0.5s linear forwards infinite;
        -ms-animation: scratchy 0.5s linear forwards infinite;
        -o-animation: scratchy 0.5s linear forwards infinite;
    }

    @keyframes scratchy {
        0% {
            background-position: 0 0;
        }
        25% {
            background-position: 0 0;
        }
        26% {
            background-position: 40px -40px;
        }
        50% {
            background-position: 40px -40px;
        }
